import React, { useState } from 'react';
import { isMobile } from 'react-device-detect';
import { makeStyles, Button, Grow, Fade } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import EcoIcon from '@material-ui/icons/Eco';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import fem from '../assets/iconos/fem.png';
import colores from '../styles/colors';
import Tooltip from '@material-ui/core/Tooltip';
import FingerprintIcon from '@material-ui/icons/Fingerprint';
import beg from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';

const PaperText = () => {
	const styles = useStyles();

	const [ejeType, setEjeType] = useState('info');

	return (
		<>

			<Paper  elevation={3} className={styles.paperDesc2}>
			
				<TextEffect active={ejeType} />
			</Paper>
			<div className={styles.ejesButton}>
				{ejesCamp.map((eje, i) => {
					return (
						<Tooltip
							TransitionComponent={Fade}
							//TransitionProps={{ timeout: 600 }}
							title={eje.name}
						>
							<Grow in={true} timeout={700 * i + 800}>
								<Fab
									style={{ backgroundColor: eje.color }}
									aria-label={eje.name}
									size='medium'
									onClick={() => {
										setEjeType(eje.key);
									}}
								>
									{eje.icon}
								</Fab>
							</Grow>
						</Tooltip>
					);
				})}
			</div>
		</>
	);
};

const TextEffect = ({ active }) => {
	const classes = useStyles();
	return (
		<div>
			{Object.keys(texts).map((key) => {
				if (key === active) {
					return (
						<Grow in={true} timeout={500}>
							<Typography className={classes.textP}  >
								{texts[key]}
							</Typography>
						
						</Grow>
					);
				}
			})}
		</div>
	);
};


const ejesCamp = [
	{ key: 'info', icon: <FingerprintIcon />, name: 'info', color: colores.ejesBlue },
	{ key: 'ambiente', icon: <EcoIcon  />, name: 'Medio Ambiente', color: colores.ejesGreen },
	{ key: 'deportes', icon: <SportsSoccerIcon  />, name: 'Deportes', color: colores.ejesOrange },
	{
		key: 'genero',
		icon: <Avatar src={fem} style={{height: 24, width: 24}} />,
		name: 'Género y Diversidad',
		color: colores.ejesPurple,
	},
];

const texts = {
	info: `me llamo Jorgelina Mudallel, me dicen Jorita, naci y me crie en barrio roma, desde muy chica camino la ciudad y siempre senti la vocacion de ayudar al otro, de participar, involucrarme en un fuerte compromiso social.
decidi estudiar sociologia para comprender los problemas sociales y entender la sociedad en la que vivo, pero senti la necesidad de tener un rol mas comprometido con mi ciudad, 
entendi que la politica es la verdadera herramienta de transformacion de la realidad, por eso vengo a proponerles un proyecto que incluya a todas las voces,
formar una sociedad inclusiva, sustentable, productiva y con igualdad de oportunidades, una sociedad en la que todos y todas podamos crecer.`,
	ambiente:
		`Trabajo a través de diferentes proyectos y campañas en la construcción de una conciencia colectiva en materia de reciclaje, reutilización y cuidado de nuestro ambiente, 
		como eje fundamental para el desarrollo sustentable de nuestra ciudad.`,
	deportes:
		` El deporte es una de las herramientas fundamentales hacia la inclusión, formación y contención de niños, niñas y 
		adolescentes. El rol que ocupan debe ser reivindicado y defendido desde acciones concretas que los auxilien.`,
	genero:
		'Desde mi lugar, formo parte de la lucha por el empoderamiento e igualdad de oportunidades para mujeres e identidades disidentes de nuestra ciudad',
};

const useStyles = makeStyles((theme) => ({
	paperDesc2: {
		height:  isMobile ? 190 : 240,
		overflow: 'auto',
		padding: isMobile ? 14 : 25,
		borderWidth: 8,
		borderColor: 'red',
		borderRadius: 15,
		backgroundColor: 'rgba(228, 235, 241, 0.8)',
		color: '#4f4f4f',
		textAlign: isMobile ? 'center' : 'justify',
		 overflowY: "auto",
 
  listStyle: "none",
  '&::-webkit-scrollbar': {
    width: '0em',
  },
  '&::-webkit-scrollbar-track': {
    boxShadow: 'inset 0 0 6px rgba(0,0,0,0.00)',
    webkitBoxShadow: 'inset 0 0 6px rgba(0,0,0,0.00)'
  },
  '&::-webkit-scrollbar-thumb': {
    backgroundColor: 'rgba(0,0,0,.2)',
    outline: '0px solid slategrey',
	
  }
	},
	ejesButton: {
		marginTop: isMobile ? -18 : -25,
		width: isMobile ? '75%' : '50%',
		marginRight: 'auto',
		marginLeft: 'auto',
		display: 'flex',
		justifyContent: 'space-between',
	},
	iconSize: { height: 28, width: 28 },
	textP: {
		fontSize: 19,
			
	}
	
}));

export default PaperText;
