import React, { useState } from 'react'
import { makeStyles, IconButton, Typography } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer';
import CloseIcon from '@material-ui/icons/Close';
import colores from '../styles/colors';
import EjesModal from './ejesModal';
import ProyectosModal from './proyectModal'
import MyModal from './myModal'
import NestedList from './menuList'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import Collapse from '@material-ui/core/Collapse';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';


const MobileMenu = () => {
    const classes = useStyles();
    const [openMenu, setOpenMenu] = useState(false);
   
    return <>
    
    <Fab onClick={() => setOpenMenu(true)}>
        <MenuIcon  />
        
    </Fab>
    
    <Drawer
        anchor='left'
        open={openMenu}
        onClose={()=> setOpenMenu(false)}
       
        
    >
        <div 	className={classes.content} > 
       
        <IconButton
            className={classes.exitBtn}
            edge='end'
            color={colores.mainDarkfull}
            onClick={()=> setOpenMenu(false)}
            aria-label='close'
        >
            <CloseIcon />
        </IconButton>
        <div style={{height: '8%'}} />
       
        <NestedList />
        </div>
    
    </Drawer>
  
    
    </>
}

const useStyles = makeStyles((theme) => ({
    content: { 
        
        width: '100%',
        height: '100%',
        backgroundColor: '#f2f2f2', //colores.fondoLight
        
    },
    exitBtn: {backgroundColor: colores.mainDark, margin: 15},
    list: {
       backgroundColor: 'red', alignSelf: 'center'
    },
    btnPr: {
        
       
        height: 80,
        backgroundColor: 'blue',
        
       
        marginTop: '25%',
        backgroundColor: 'red'
    },
    textBtn: {
        fontSize: 24,
        letterSpacing: 2,
        textAlign: 'center',
    },
    nested: {
    paddingLeft: theme.spacing(4),
  },
}))

export default MobileMenu;