import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import Divider from '@material-ui/core/Divider';
import ShareIcon from '@material-ui/icons/Share';
import DashboardIcon from '@material-ui/icons/Dashboard';
import BookmarksIcon from '@material-ui/icons/Bookmarks';
import FacebookIcon from '@material-ui/icons/Facebook';
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';
import colores from '../styles/colors';
import EjesModal from './ejesModal';
import ProyectosModal from './proyectModal'
import MyModal from './myModal'
import YouTubeIcon from '@material-ui/icons/YouTube';
import TwitterIcon from '@material-ui/icons/Twitter'
import InstagramIcon from '@material-ui/icons/Instagram';
const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

export default function NestedList() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
	const [ejesHandler, setEjesHandler] = useState(false);
	const [proyectHandler, setProyectHandler] = useState(false);
	const [myHandler, setMyHandler] = useState(false);
   
  const handleClick = () => {
    setOpen(!open);
  };

  const mainButtons = [
		{
            id: 'prov',
			icon: <VerifiedUserIcon />,
			name: 'accion con Provincia',
			func: () => setMyHandler(false),
		},
		{
             id: 'ejes',
			icon: <DashboardIcon />,
			name: 'ejes de campaña',
			func: () => setEjesHandler(true),
		},
		{
             id: 'proyectos',
			icon: <BookmarksIcon />,
			name: 'proyectos HCSF',
			func: () => setProyectHandler(true),
		},
        {
            id: 'redes',
			icon: <ShareIcon />,
			name: 'Redes Sociales',
			func: () => handleClick(),
		},
	];

    const redesItem = [
        {name: 'Facebook', icon: <FacebookIcon />,color: '#3b5998', url: 'https://www.facebook.com/jorgelinamudallel/' },
        {name: 'Instagram', icon: <InstagramIcon />,color: '#d11563', url: 'https://www.instagram.com/jormudallel/' },
        {name: 'Twitter', icon: <TwitterIcon /> ,color: '#00aced', url: 'https://twitter.com/JoritaMudallel'} ,
         {name: 'YouTube', icon: <YouTubeIcon />,color: ' #bb0000', url: 'https://www.youtube.com/channel/UCruszoz-TRFXYXr0SWXScfg'},
    ]

  return (<>
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          Jorgelina Mudallel
        </ListSubheader>
      }
      className={classes.root}
    >
    {mainButtons.map((item) => (
        <ListItem button onClick={() => item.func()} style={{marginBottom: 15}}>
        <ListItemIcon>
         {item.icon}
        </ListItemIcon>
        {item.id !== 'redes' ? (<ListItemText primary={item.name} />) : (<>
            <ListItemText primary={item.name} />
            {open ? <ExpandLess /> : <ExpandMore />}
       </> )}
      </ListItem>
    ))}

      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
            {redesItem.map((item) => (
                <ListItem button className={classes.nested} onClick={() => window.open(item.url)}>
            <ListItemIcon>
              {item.icon}
            </ListItemIcon>
            <ListItemText primary={item.name} />
          </ListItem>
            ))}
          
        </List>
      </Collapse>
    </List>

    <EjesModal
      open={ejesHandler}
      handleClose={() => setEjesHandler(false)}
    />
    
    <ProyectosModal open={proyectHandler} handleClose={() => setProyectHandler(false)}
		/>

    <MyModal open={myHandler} handleClose={() => setMyHandler(false)}
		/>

  </>);
}



