import React, { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Grid, IconButton, makeStyles, CardContent, CardActionArea, Tab, Paper  } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import AppBar from '@material-ui/core/AppBar';
import colores from '../styles/colors'
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardMedia from '@material-ui/core/CardMedia';
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import obras from '../assets/ejes/obr.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';
import VerticalTabs from './verticalTabs'
import CustomizedTabs from './contt'
import portadaJor from '../assets/portyt.png'

const MyModal = ({ open, handleClose }) => {
	const classes = useStyles();
	


	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			
			>
				<AppBar className={classes.appBar}>
					<Toolbar style={{backgroundColor: colores.mainLightfull}}>
						<IconButton
							edge='start'
							color={colores.mainDarkfull}
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Accion Con Provincia
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{ 
					height: '100%',
					paddingLeft: isMobile ? 20 : 100, 
					paddingRight: isMobile ? 20 : 0, 
					paddingTop: 20, 
					backgroundColor: colores.fondoLight, 

				}}>
				
					{/* <CustomizedTabs /> */}
					
					<VerticalTabs />
				
				</div>
			</Dialog>
		</div>
	);
};

const ContentJor = () => {
	const [btnProv, setBtnProv] = useState('beg')
	return (
		<Grid  container spacing={3}   >
			<Grid item xs={12} sm={2} style={{display: 'flex', flexDirection: isMobile ? 'row' : 'column'}}  >
			{provBtn.map((btn) => (
					<ProvCard btn={btn} btnProv={btnProv} setBtnProv={setBtnProv}  />
			))}
		
			</Grid>
            <Grid item xs={12} sm={9} wrap="nowrap">
				<Card style={{height: '100%'}}>
					<Card>
						<CardContent>
						Boleto Educativo Gratuito
						</CardContent>
					</Card>
				</Card>
			</Grid>
			
		</Grid>
	);
};


const HacemosBanner = () => {
	const classes = useStyles();

	return <div className={classes.hacemosCont}>
		<img src={portadaJor} style={{height: '200%', 
		textAlign: 'center'
		}}></img>
	</div>
}


const ProvCard = ({btn, btnProv, setBtnProv}) => {

	return <Card onClick={() => setBtnProv(btn.id)} elevation={3} style={{ backgroundColor: btn.id === btnProv ? '#0c395c' : '#fff', marginBottom: 6, padding: 6, borderRadius: 6,  height: 120, width: isMobile ? '25%' : '70%', alignSelf: 'center', cursor: 'pointer'}}>
	
	<CardMedia style={{ height: '100%' }}
		image={btn.img}
		title={btn.name} >
			
		</CardMedia>	
	
	
	
	</Card>
}

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});


const provBtn = [
	{id: 'beg', img: begIcon, name: 'Boleto Educativo Gratuito'},
	{id: 'billetera', img: billetera, name: 'billetera Santa Fe'},
	{id: 'incluir', img: incluir, name: 'Plan Incluir'},
	{id: 'vacuna', img: vacuna, name: 'Plan de Vacunacion'},
	{id: 'obras', img: obras, name: 'Obras Publicas'}
]

const useStyles = makeStyles((theme) => ({
	appBar: {
		position: 'sticky',
	},

	title: {
		marginLeft: 5,
		color: colores.mainDarkfull
	},
	tabs: {
		borderRight: `1px solid ${theme.palette.divider}`,
	  },
	hacemosCont: {
		marginLeft: 30,
		width: '100%',
		height: 170,
		marginBottom: 30,
		overflow: 'hidden',
		marginLeft: 33
	}
}));

export default MyModal;
