import React, { forwardRef, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Box, Grid, IconButton, makeStyles, Avatar } from '@material-ui/core';
import { FacebookProvider, Page, EmbeddedPost, CustomChat } from 'react-facebook';
import AmbienteContent from './ejesContent/ambiente';
import DeportesContent from './ejesContent/deportes';
import GeneroContent from './ejesContent/genero';
import BarriosContent from './ejesContent/barrios';
import EjesContent from '../custom/ejesContent'
import Grow from '@material-ui/core/Grow';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import Toolbar from '@material-ui/core/Toolbar';
import CloseIcon from '@material-ui/icons/Close';
import ListItemText from '@material-ui/core/ListItemText';
import AppBar from '@material-ui/core/AppBar';
import ListItem from '@material-ui/core/ListItem';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import medam from '../assets/ejes/maff.png';
import { Image } from '@material-ui/icons';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import gen from '../assets/ejes/genero.jpg';
import obr from '../assets/ejes/obr.png';
import ecoAm from '../assets/ejes/maff.png';
import noi from '../assets/noimg.jpg';
import colores from '../styles/colors';
import port from '../assets/portyt.png'
import begIcon from '../assets/ejesProv/begicon.jpg'
import EcoIcon from '@material-ui/icons/Eco';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import fem from '../assets/iconos/fem.png';
import deportes from '../assets/ejes/deportes.jpg'
import barriosJor from '../assets/ejes/barriosJor.jpg'
import DomainIcon from '@material-ui/icons/Domain';

const EjesModal = ({ open, handleClose }) => {
	const [swCont, setSwCont] = useState('ambiente');
	const classes = useStyles();

	function switchContent(cont){
		switch (cont) {
			case 'ambiente':
				return <AmbienteContent />
				break;

			case 'deporte':
				return <DeportesContent />
				break;

			case 'genero':
				return <GeneroContent />
			break;

			case 'barrios':
				return <BarriosContent />
			break;

			default:
				return <></>
				break;
		}
	}

	
	return (
		<div>
			<Dialog
				fullScreen
				open={open}
				onClose={handleClose}
				TransitionComponent={Transition}
			
			>
				<AppBar className={classes.appBar}>
					<Toolbar style={{backgroundColor: colores.mainLightfull}}>
						<IconButton
							edge='start'
							color={colores.mainDarkfull}
							onClick={handleClose}
							aria-label='close'
						>
							<CloseIcon />
						</IconButton>
						<Typography variant='h6' className={classes.title}>
							Ejes De Trabajo
						</Typography>
					</Toolbar>
				</AppBar>
				<div style={{height: '100%', paddingLeft: isMobile ? 12 : 90, paddingRight: isMobile ? 12 : 90, paddingTop: 20, backgroundColor: colores.fondoLight, textAlign: 'center' }}>
					<ContentEjes setSwCont={setSwCont} swCont={swCont} />
					
					<Divider style={{marginTop: 25, marginBottom: 25}} />
					<EjesContent>
						{switchContent(swCont)}
					</EjesContent>
					{/* {!isMobile &&  <ContentFacebook /> } */}
					
				
				</div>
			</Dialog>
		</div>
	);
};

const ContentFacebook = () => {

	const FbPost = [
		'https://www.facebook.com/jorgelinamudallel/posts/4102542703165503',
		'https://www.facebook.com/jorgelinamudallel/posts/4157726427647130',
		'https://www.facebook.com/jorgelinamudallel/posts/4195750497178056',
	//	'https://www.facebook.com/jorgelinamudallel/posts/4177032202383219'
	]

	return <FacebookProvider appId="865953314130137" >
	<Grid container spacion={3} style={{alignContent: 'center'}}>
		{FbPost.map((post) => (
			<Grid item  sm={4}>
			<EmbeddedPost width={isMobile ? 200 : 250}  href={post} />
			</Grid>
		))}
		
		
		
	</Grid>
	</FacebookProvider> 
}

const ContentEjes = ({setSwCont, swCont}) => {
	return (<>
		
		<Grid container spacing={ isMobile ? 2 : 4}  style={{ width: isMobile ? '100%' : '85%', marginRight: 'auto', marginLeft: 'auto', }} >
			{ejesC.map((eje, i) => (
					 <Grow  in={true} mountOnEnter  timeout={400 * i + 400}>
				<Grid item xs={3} sm={3} >
					<CardEjes eje={eje} setSwCont={setSwCont} swCont={swCont}  />
				</Grid>
					</Grow>
			))}
		</Grid>
	</>);
};



const CardEjes = ({eje, setSwCont, swCont}) => {
	
	return (
		<Card  elevation={swCont === eje.id ? 1 : 5} style={{ 
			//width: isMobile && 80,
			padding: isMobile ? 2 : 0,  
			marginLeft: eje.id === 'ambiente' && -4,
			borderRadius: isMobile ? 10 : 15, 
			textAlign: 'center', color: swCont !== eje.id && eje.color, 
			backgroundColor: swCont === eje.id ? eje.color : colores.mainLight
		}} onClick={() => setSwCont(eje.id)}>
			<CardActionArea>
				{isMobile ? eje.ico : (
					<CardMedia
					style={{ height: 100, marginTop: 10,marginRight: 10, marginLeft: 10, borderRadius: 15 }}
					image={eje.img}
					title={eje.title}
				/>
				)}
					<Typography gutterBottom variant='h5' style={{fontSize: isMobile ? 14 : 18, marginTop: 5, color: eje.letra}}>
						{eje.name}
					</Typography>			
			</CardActionArea>
			
		</Card>
	);
};

const ejesC = [
	{ id: 'ambiente', name: isMobile ? 'Ambiente' : 'Medio Ambiente', ico: <EcoIcon/>, img: ecoAm,letra: colores.cardGrTex2, letra: colores.cardGradiant2full, color: colores.cardGradiant2, borde: colores.cardGrTex2 },
	{ id: 'deporte',  name: 'deportes', ico: <SportsSoccerIcon/>, img: deportes, letra: colores.cardGrTex3, color: colores.cardGradiant3, borde: colores.cardGrTex3 },
	{ id: 'genero',  name: 'Género', ico:  <Avatar src={fem} style={{height: 24, width: 24, margin: 'auto', marginTop: 2 }} />, img: gen, letra: colores.cardGrTex1, color: colores.cardGradiant1, borde: colores.cardGrTex1 },
	{ id: 'barrios',  name: 'Barrios', ico:  <DomainIcon/>, img: barriosJor, letra: colores.cardGrTex4, color: colores.cardGradiant4, borde: colores.cardGrTex4 },
];

const Transition = forwardRef(function Transition(props, ref) {
	return <Slide direction='up' ref={ref} {...props} />;
});

const useStyles = makeStyles(() => ({
	appBar: {
		position: 'sticky',
	},

	title: {
		marginLeft: 5,
		color: colores.mainDarkfull
	},
}));

export default EjesModal;
