import React, {Image} from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { Card, Paper, Grid } from '@material-ui/core';
import hsf from '../assets/ejesProv/hsf.jpg'
import portadaJor from '../assets/portyt.png'
import colores from '../styles/colors'
import begIcon from '../assets/ejesProv/begicon.jpg';
import billetera from '../assets/ejesProv/bille.png';
import obras from '../assets/ejes/obr.png';
import vacuna from '../assets/ejesProv/vacSF.png';
import incluir from '../assets/ejesProv/incluir.jpg';
import Avatar from '@material-ui/core/Avatar';

const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    backgroundColor: 'transparent',
    '& > span': {
      maxWidth: 60,
    
      width: '100%',
      backgroundColor: '#635ee7',
    },
  },
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <span />}} />);

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: 'none',
    color: '#fff',
    fontWeight: theme.typography.fontWeightRegular,
    fontSize: theme.typography.pxToRem(15),
    marginRight: theme.spacing(1),
    '&:focus': {
      opacity: 1,
    },
  },
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: theme.palette.background.paper,
  },
  demo2: {
    backgroundColor: '#2e1534',
  },
}));

export default function CustomizedTabs(){

    
    return  <div style={{height: 80, width: '90%', display: 'flex'}}>
      { dataA.map((item) => (
            <Paper elevation={3} style={{
                width: '24%',
                marginLeft: 20, 
                backgroundColor: item.fondo, 
                borderRadius: 15, padding: 10,
                display: 'flex',
                //flexDirection: 'column', 
               // alignItems: 'center', 
                //alignContent: 'center',
                }}>
                <Avatar src={item.img} variant="rounded" style={{height: 60, width: 60}} />
                {/* <Typography>
                    {item.name}
                </Typography> */}
            </Paper>
        )) }
    </div>
    
    
}

/* export default  */function CustomizedTabs2() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  
  return (
    <div className={classes.root}>
    
      <div className={classes.demo2}>
        <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
          <StyledTab label="Workflows" />
          <StyledTab label="Datasets" />
          <StyledTab label="Connections" />
        </StyledTabs>
        <Typography className={classes.padding} />
      </div>
    </div>
  );
}


const dataA = [
	{id: 'beg', img: begIcon, name: 'Boleto Educativo Gratuito', fondo: colores.cardGradiant1},
	{id: 'billetera', img: billetera, name: 'billetera Santa Fe', fondo: colores.cardGradiant2},
	{id: 'incluir', img: incluir, name: 'Plan Incluir', fondo: colores.cardGradiant3},
	{id: 'vacuna', img: vacuna, name: 'Plan de Vacunacion', fondo: colores.cardGradiant4},
	{id: 'obras', img: obras, name: 'Obras Publicas', fondo: colores.cardGradiant1},
]
