import React, {  } from 'react'
import { Paper, Typography, Grow } from '@material-ui/core';
import colores from '../../styles/colors';
import { isMobile } from 'react-device-detect';



const DeportesContent = () => {


    return <Grow in={true} timeout={500}>
        <Paper style={{padding: 20,margin: 'auto',marginBottom: 40, width: isMobile ? '100%' : '80%',textAlign: isMobile ? 'center' : 'justify', backgroundColor: colores.cardGrIco2}}>
            <Typography style={{fontSize: isMobile ? 20 : 24, letterSpacing: 1.4}} >
                El deporte es una de las herramientas fundamentales hacia la inclusión, formación y contención de niños, niñas y 
                adolescentes. El rol que ocupan debe ser reivindicado y defendido desde acciones concretas que los auxilien.
            </Typography>
        </Paper>
    </Grow>
}


export default DeportesContent;



